* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(61, 157, 221);
}

.boxTarefas {
  width: 80%;
  max-width: 800px;
  background-color: cornsilk;
  padding: 30px 50px;
  margin: 10px auto;
  border-radius: 10px;
}

.boxTarefas h2 {
  text-align: center;

}

.boxTarefas p {
  cursor: pointer;
  font-size: 15px;
  padding: 8px 10px;
  border-bottom: 2px dotted #ccc;
}



.addTarefa {
  position: fixed;
  right: 10px;
  bottom: 10px;
  background-color: cornflowerblue;
  width: 50px;
  height:50px;
  cursor: pointer;
  border-radius: 25px;
  color: beige;
  text-align: center;
  font-size: 30px;
  line-height: 50px;
}

.modal {
  position: fixed;
  left:0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.205);
  z-index: 9999;
}

.modalContent {
  max-width: 600px;
  padding: 40px 20px;
  background-color: rgb(255, 255, 255);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}